
.delivery {
    background-image: url(../../images/del-pic.png);
    background-repeat: no-repeat;
    background-position: bottom right;
    padding-left: 96px;
}

.delivery__title {
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
    margin: 120px 0 56px 0;
    font-family: 'Roboto Flex';
}

.delivery__content {
    width: 992px;
    padding-bottom: 186px;
}

.delivery__el {
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    padding-bottom: 16px;
    border-bottom: 1px solid white;
    margin: 16px 0;
}
