.design {
    font-family: 'Roboto Flex';
    display: flex;
    flex-direction: column;
    padding: 0 96px;
}

.design__image {
    width: 1050px;
    height: 1050px;
    position: absolute;
    top: 15px;
    align-self: center;
}

.design__title {
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
    z-index: 5;
    position: relative;
    margin: 120px 0 72px 0;
}

.design__subtitle {
    z-index: 5;
    position: relative;
    width: 565px;
    font-size: 64px;
    font-style: normal;
    font-weight: 700;
    line-height: 72px;
    margin: 0 0 262px 0;
}

.design__text {
    width: 1327px;
    font-size: 48px;
    font-style: normal;
    font-weight: 500;
    line-height: 56px;
    text-align: center;
    z-index: 5;
    position: relative;
    margin: 0 auto;
}

.look__title {
    font-size: 96px;
    font-style: normal;
    font-weight: 500;
    line-height: 104px;
    margin: 0;
}

.look {
    margin: 120px 0 0 0;
}

.look__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.look__arrows {
    align-self: flex-end;
    display: flex;
    gap: 56px;
    margin: 0 96px 0 0;
}

.look__arrow_left {
    transform: scale(-1);
    border-top: 1px solid white;
    padding: 10px 10px 10px 52px;

}

.look__arrow_right {
    border-bottom: 1px solid white;
    padding: 10px 10px 10px 52px;
}


.look__gallery {
    margin: 96px 0 120px 96px;
}

.shop__content {
    display: grid;
    grid-template-columns: 448px 448px;
    grid-template-rows: 460px 460px;
    margin: 96px auto;
    column-gap: 290px;
    row-gap: 96px;
    width: 1198px;
}

.shop__grafik {
    margin-top: 95px;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 38px;
}