
.contact {
    background-image: url(../../images/contact-pic.png);
    background-repeat: no-repeat;
    background-position: right bottom;
    padding: 0 96px;
    font-family: 'Roboto Flex';
    height: 100%;
    margin-top: auto;

}

.contact__title {
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
    margin: 120px 0 72px;
}

.contact__content {
    display: flex;
    flex-direction: row;
    gap: 96px;
    margin: 0;
}

.contact__column {
    width: 386px;
}

.column {
    list-style-type: none;
    padding: 0;
}


.contact__column_title {
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 48px;
    padding-bottom: 16px;
    border-bottom: 1px solid white;
    margin-top: 32px;
}

.column__name {
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    margin-top: 12px;
}

.column__adress {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__tc {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__phone {
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    margin-top: 12px;
}

.column__email {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__grafik {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__contact {
    display: flex;
    flex-direction: row;
    width: 88px;
    justify-content: space-between;
    margin-top: 26px;
}

.column__icon {
    width: 32px;
    height: 32px;
}