
.cooperation {
    padding: 120px 0 178px 96px;
}

.cooperation__title {
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
    z-index: 5;
    position: relative;
    margin: 0 0 56px 0;
}

.cooperation__image {
    position: absolute;
    right: 0;
    top: 5px;

}

.cooperation__content {
    z-index: 5;
    position: relative;
    width: 856px;
}

.cooperation__text {
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: 34px;
}

.cooperation__link {
    font-size: 28px;
    font-style: normal;
    font-weight: 900;
    line-height: 34px;
}