
.footer {
    margin: 120px 96px;
}


.footer__links {
    display: flex;
    flex-direction: row;
    gap: 56px;
}

.footer__link {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 2.4px;
    text-decoration: none;
    color: white;
}

.footer__icons {
    display: flex;
    gap: 24px;
    align-items: center;
}

.footer__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.footer__contact {
    display: flex;
    justify-content: space-between;
    margin-top: 96px;

}

.footer__contact_info {
    width: 368px;
}

.footer__logo {
    margin-top: 146px;
}

.footer__text {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 26px;
    margin: 0;
}

.footer__span {
    font-weight: 700;
}

.footer__pravila {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 26px;
    margin: 24px 0;
}

.footer__copy {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 26px;
}


@media screen and (max-width: 1350px) {
    .footer {
        margin: 96px 86px;
    }
    
    .footer__links {
        gap: 32px;
    }

    .footer__link {
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 40px; /* 125% */
        letter-spacing: 1.92px;
    }

}

@media screen and (max-width: 1100px) {
    .footer {
        margin: 96px 40px;
        display: flex;
        flex-direction: row;
    }
    
    
.footer__links {
    display: flex;
    flex-direction: column;
    gap: 48px;
}

  

}